:root {
  --primary-color: #212121;
  --secondary-color: #7732ad;
  --password-field-color: #8c7c9b;
  --background-color: #261c33;
  --white: #ffffff;
  --modal-color: #aaa2b3;
}
.App {
  text-align: center;
}
html {
  box-sizing: border-box;
}
body {
  background-color: var(--background-color);
  color: var(--white);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  animation: backgroundColorAnimation 5s infinite alternate;
}

h3 {
  margin: 0 0 20px;
  text-align: center;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--primary-color);
  width: 400px;
  height: 480px;
  border-radius: 15px;
  padding: 30px 30px 50px;
  box-shadow: 0px 10px 10px 15px rgba(104, 106, 164, 0.75);
}

.password-field {
  background-color: var(--password-field-color);
  display: flex;

  position: relative;
  font-size: 15px;
  font-weight: 600;
  padding: 12px 10px;
  height: 55px;
  width: 100%;
  padding: 10px 15px;
  border: 3px solid var(--secondary-color);
  box-sizing: border-box;
  border-radius: 15px;
}

.password-field .passwrd_button {
  position: absolute;
  top: 30%;
  right: 0.3rem;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  font-size: 20px;
  margin-top: 10px;
}

.passwrd_button {
  display: block;
  width: 100%;
  border: none;
  background-color: var(--secondary-color);
  color: var(--white);
  font-size: 18px;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 10px;
  font-weight: 600;
  margin-top: 20px;
}

.currentLength {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  margin-top: 1.5rem;
  font-weight: 500;
}

.option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  border: 2px solid rgba(60, 60, 60, 0.8);
  padding: 0.6rem;
  border-radius: 10px;
  font-weight: 500;
}

.checkbox {
  transform: scale(1.8); /* Adjust the scale factor */
}

.number {
  font-weight: 600;
  transform: scale(1.1);
}

.btn#generate {
  margin: 20px 0;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 15px;
}
.modal.show {
  display: block;
}
.modal-content {
  border-radius: 10px;
  background-color: var(--modal-color);
  margin: 50% auto;
  padding: 10px;
  width: 30%;
  text-align: center;
}

@keyframes backgroundColorAnimation {
  from {
    background-color: #aeaed1; /* Starting background color */
  }
  to {
    background-color: #261c33; /* Ending background color */
  }
}
